import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {ProductService} from "../../../product.service";
import { Router } from '@angular/router';
import {ProductDetailsComponent} from "../product-details/product-details.component";
import {filter} from "rxjs";
import {PanierService} from "../../../panier.service";

export class Product{
  id: number;
  name: string;
  price: number;
  availability: boolean;
  imageUrl: string;
  description: string;
  quantity:number=1;


  constructor(id: number, name: string, price: number, availability: boolean, imageUrl: string, description: string) {
    this.id = id;
    this.name = name;
    this.price = price;
    this.availability = availability;
    this.imageUrl = imageUrl;
    this.description = description;
  }
}
  @Component({
    selector: 'app-list-product',
    templateUrl: './list-product.component.html',
    styleUrls: ['./list-product.component.css']
  })
  export
  class
  ListProductComponent implements OnInit{
  products: Array<Product> | undefined = this.productService.getAll();
  produ :any[]=[];
  categories :any[]=[];
  cart:any[]=[];
    searchTerm: any;
    private filteredProducts: any[] | undefined;
    @Input() data:any={};
    @Output() item=new EventEmitter();
    addButton:boolean=false;
    amount: number=0;
    getColor(product: Product): string {
      return product.availability ? "green" : "red";
    }
    getState(product: Product): String {
      if (product.availability === true) {
        return "En_Stock";
      } else {
        return "En_rupture_de_Stock";
      }
    }
    constructor(private productService:ProductService,private router:Router,private penierservice:PanierService) {
    }
    ngOnInit(): void {
         //this.products=this.productService.products;
      this.getProducts();
      this.getCategories();

      }
      goToDetails(productId : number){
        this.router.navigate(["products",productId ]);
      }
      getProducts(){
      this.productService.getAllProducts().subscribe((res:any)=>{this.produ=res});
      }

    getCategories(){
      this.productService.getAllCategories().subscribe((res:any)=>{this.categories=res});
    }
    filtercategory(event :any){
      let value=event.target.value;
      if(value=="all"){
        this.getProducts();
      }
      this.getProductsByCategory(value);
      console.log(value);

  }
  getProductsByCategory(keyword:String){
      this.productService.getProductByCategory(keyword).subscribe((res:any)=>{this.produ=res});
  }

    searchProducts() {
      if (!this.searchTerm) {
        this.getProducts();
      } else {
        this.filteredProducts = this.produ.filter(product =>
          product.title.toLowerCase().includes(this.searchTerm.toLowerCase())
        );
      }
    }
    addToCart(event: any) {
      if ("panier" in localStorage) {
        this.cart = JSON.parse(localStorage.getItem("panier")!) as Product[];
        this.cart.push(event);
        localStorage.setItem("panier", JSON.stringify(this.cart));
      } else {
        this.cart = [event];
        localStorage.setItem("panier", JSON.stringify(this.cart));
      }
      console.log(event);
    }




  }
